.footer {
    background: rgba($color: $blue, $alpha: 0.1);
    &-top {
        border-bottom: 1px solid rgba($color: #335253, $alpha: 0.08);
        padding: 50px 0 30px;
        .logo {
            img {
                width: 96px;
                height: auto;
            }
        }
        .subscribe-email {
            max-width: 370px;
            width: 100%;
            p {
                color: #2a2d44;
                font-weight: 700;
                margin-bottom: 10px;
            }
            .form-control {
                height: 42px;
                font-size: 14px;
                &::placeholder {
                    color: #b0b0b0;
                }
            }
            .btn-submit {
                background: $copper;
                color: #fff;
                font-size: 18px;
                position: absolute;
                right: 0;
                top: 0;
                height: 42px;
                width: 42px;
            }
        }
    }
    &-main {
        border-bottom: 1px solid rgba($color: #335253, $alpha: 0.08);
        padding: 35px 0;
        .web-info{
            max-width: 320px;
        }
        .web-title {
            color: #2a2d44;
            font-family: "EB Garamond", sans-serif;
            font-size: 16px;
            margin-bottom: 1rem;
        }
        .info-text {
            color: #3e3d3c;
            font-size: 14px;
            margin-bottom: 10px;
            a {
                color: #3e3d3c;
            }
            &:last-of-type {
                margin-bottom: 0;
            }
        }
    }
    &-menu {
        .menu-title {
            font-size: 16px;
            margin-bottom: 1rem;
        }
        ul {
            > li {
                &:not(:last-of-type) {
                    margin-bottom: 10px;
                }
                a {
                    color: #3e3d3c;
                    font-size: 14px;
                    &:hover {
                        color: $copper;
                    }
                }
            }
        }
        &-1 {
            max-width: 300px;
            width: 100%;
            ul {
                display: flex;
                flex-wrap: wrap;
                > li {
                    width: 50%;
                }
            }
        }
        &-2 {
            max-width: 120px;
            width: 100%;
        }
    }
    &-menu-container {
        justify-content: flex-end;
        flex: 1;
    }
    &-bottom {
        padding: 20px 0;
        justify-content: space-between;
        .copyright {
            font-size: 14px;
        }
        .policy-links {
            > li {
                &:last-of-type {
                    margin-right: 30px;
                }
                &:not(:last-of-type) {
                    margin-right: 16px;
                    padding-right: 16px;
                    position: relative;
                    &:after {
                        content: "";
                        background: rgba($color: #335253, $alpha: 0.15);
                        height: 16px;
                        width: 1px;
                        position: absolute;
                        right: 0;
                        top: 50%;
                        transform: translateY(-50%);
                    }
                }
                a {
                    color: #3e3d3c;
                    display: inline-block;
                    font-size: 14px;
                    &:hover {
                        color: $copper;
                    }
                }
            }
        }
        .social-links {
            gap: 24px;
            a {
                color: $blue;
                display: inline-block;
                &:hover {
                    transform: translateY(-4px);
                }
            }
        }
    }
}
@media (max-width: 992px) {
    .footer {
        &-menu-container {
            flex: unset;
            justify-content: flex-start;
            margin-top: 30px;
            width: 100%;
        }
        &-bottom {
            .copyright {
                font-size: 14px;
                text-align: center;
                width: 100%;
                margin-top: 16px;
            }
            .link-wrap {
                justify-content: space-between;
                order: -1;
                width: 100%;
            }
        }
        &-menu{
            &-2, &-3 {
                .menu-title{
                    margin-top: 1rem;
                }
            }
        }
    }
}
@media (max-width: 767px) {
    .footer {
        &-menu {
            &-1 {         
                .menu-title{
                    margin-top: 1rem;
                }
            }
            
        }
        &-bottom {
            .link-wrap {
                flex-wrap: wrap;
                justify-content: center;
            }
            .policy-links {
                > li {
                    &:not(:last-of-type) {
                        &:after {
                            height: 42px;
                        }
                    }
                    &:last-of-type {
                        margin-right: 0;
                    }
                }
            }
            .social-links {
                margin-top: 10px;
            }
        }
    }
}
