@import "font";
@import "variables";

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: bold;
    line-height: 1.4;
}
a {
    text-decoration: none;
    transition: 0.3s;
}
img {
    width: 100%;
    height: auto;
    object-fit: cover;
    transition: 0.3s;
}
body {
    background: white;
    color: #525150;
    font-family: "Lato", sans-serif;
    font-size: 16px;
    font-weight: 400;
    max-width: 100%;
    overflow-x: hidden;
    line-height: 1.5;
    letter-spacing: 0.5px;
}
.section-title {
    font-family: "EB Garamond", sans-serif;
    font-size: 30px;
    font-weight: 600;
}

@media (min-width: 1400px) {
    .container {
        max-width: 1194px;
    }
}

.form-control,
.form-select {
    &:focus {
        box-shadow: unset;
    }
}

.btn {
    box-shadow: none !important;
}

@import "header";
@import "home";
@import "about";
@import "listing";
@import "product";
@import "news";
@import "contact";
@import "footer";
@import "misc";
@import "login";
@import "account";
@import "cart";
@import "checkout";
