.news-sidebar {
    .news-item {
        display: flex;
        &:not(:last-of-type) {
            margin-bottom: 1rem;
        }
        img {
            aspect-ratio: 1;
            width: 65px;
            margin-right: 1rem;
            display: block;
        }
        .title {
            color: #525150;
            font-family: -apple-system, -apple-system, BlinkMacSystemFont, Roboto, Oxygen, Ubuntu, Cantarell,
                "Open Sans", "Helvetica Neue", sans-serif;
            font-size: 14px;
            flex: 1;
            &:hover {
                color: $copper;
            }
        }
    }
}
.news-sidebar-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-bottom: 30px;
    > a {
        display: inline-block;
        font-size: 14px;
        line-height: 1;
        padding: 10px 16px;
        border: 1px solid #d7dadd;
        color: #424551;
        &:hover {
            border-color: $blue;
            background: $blue;
            color: #fff;
        }
    }
}
.news-listing {
    display: flex;
    flex-wrap: wrap;
    gap: 40px 30px;
    .news-item {
        width: calc(100% / 2 - 15px);
        border: 1px solid #eee;
        .thumbnail {
            border-bottom: 1px solid #eee;
            img {
                aspect-ratio: 1 / 0.625;
            }
        }
        .content {
            padding: 25px;
            .title {
                font-size: 20px;
                a {
                    color: #2a2d44;
                    font-size: inherit;
                    &:hover {
                        color: $copper;
                    }
                }
            }
            .title-cate{
                font-weight: 600;
                font-size: 12px;
                margin-bottom: 0.5rem;
                text-transform: uppercase;
                a{
                    color: $blue;
                }
            }
            .title-tag{
                color: $blue;
                font-weight: 500;
                font-size: 12px;
                margin-bottom: 0.5rem;
                text-transform: uppercase;
                a{
                    color: #616162;
                }
            }
            .desc {
                color: #616162;
                font-size: 14px;
            }
            .metadata {
                display: flex;
                align-items: center;
                justify-content: space-between;
                .date {
                    padding: 10px 20px;
                    background: #f9f9f9;
                    border: 1px solid #eeeeee;
                    font-size: 14px;
                    font-style: italic;
                    color: #616162;
                }
                .action {
                    a {
                        display: inline-block;
                        background: $copper;
                        padding: 10px 28px;
                        font-weight: 700;
                        font-size: 14px;
                        color: #fff;
                        &:hover {
                            background: $blue;
                            color: #fff;
                        }
                    }
                }
            }
        }
    }

    @media (max-width: 767px) {
        gap: 30px 20px;
        .news-item {
            width: 100%;
            .content {
                padding: 20px;
            }
        }
    }
}
.news-detail {
    img{
        max-width: 100% ;
        height: auto !important;
    }
    padding: 30px 0;
    .page-breadcrumb {
        margin-bottom: 30px;
    }
    .entry-heading {
        padding-bottom: 20px;
        border-bottom: 1px solid #e5e8ed;
        max-width: 920px;
        margin: 0 auto 24px;
        h1 {
            font-family: "EB Garamond", sans-serif;
            font-size: 38px;
            margin-bottom: 20px;
        }
        .date {
            color: #9a9ca5;
        }
    }
    .entry-content {
        padding-bottom: 20px;
        max-width: 920px;
        margin: 0 auto;
        border-bottom: 1px solid #e5e8ed;
        blockquote {
            border-left: 5px solid $copper;
            padding-left: 24px;
        }
    }
    .comment-box {
        max-width: 920px;
        margin: 0 auto;
        .total-comment {
            strong {
                color: $blue;
            }
        }
        .sort-comment {
            .form-select {
                width: 140px;
                height: 44px;
            }
        }
        .comment-form {
            gap: 20px;
            .btn-submit {
                background: $copper;
                border-radius: 0;
                color: #fff;
                text-transform: uppercase;
                display: inline-flex;
                align-items: center;
                justify-content: center;
                width: 80px;
                height: 42px;
            }
        }
        &-head {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            justify-content: space-between;
            padding: 20px 0;
            border-top: 1px solid;
            border-bottom: 1px solid;
            border-color: #e5e8ed;
        }
        &-main {
            padding: 20px 0;
        }
    }
    .comment-listing {
        margin-top: 5px;
        .comment-item {
            padding: 15px 0;
            border-bottom: 1px solid rgba($color: #2a2d44, $alpha: 0.1);
            .comment {
                display: flex;
                .avatar {
                    margin-right: 10px;
                    img {
                        height: 48px;
                        width: 48px;
                    }
                }
                .content {
                    .name {
                        color: $blue;
                        font-weight: bold;
                        margin-bottom: 4px;
                        i {
                            color: $copper;
                        }
                    }
                    .text {
                        margin-bottom: 8px;
                    }
                    .metadata {
                        display: flex;
                        font-size: 14px;
                        .date {
                            color: #616162;
                            padding-right: 15px;
                            margin-right: 15px;
                            position: relative;
                            &:after {
                                content: "";
                                width: 1px;
                                height: 16px;
                                background: #7f7f7f;
                                position: absolute;
                                right: 0;
                                top: 50%;
                                transform: translateY(-50%);
                            }
                        }
                        .like {
                            padding-right: 15px;
                            margin-right: 15px;
                            position: relative;
                            &:after {
                                content: "";
                                width: 1px;
                                height: 16px;
                                background: #7f7f7f;
                                position: absolute;
                                right: 0;
                                top: 50%;
                                transform: translateY(-50%);
                            }
                            a {
                                display: inline-flex;
                                align-items: center;
                                color: #989899;
                                i {
                                    margin-right: 6px;
                                }
                                &:hover {
                                    color: $blue;
                                }
                            }
                            &.active {
                                a {
                                    color: $blue;
                                }
                            }
                        }
                        .reply {
                            a {
                                display: inline-flex;
                                align-items: center;
                                color: #989899;
                                i {
                                    margin-right: 6px;
                                }
                                &:hover {
                                    color: $blue;
                                }
                            }
                            &.active {
                                a {
                                    color: $blue;
                                }
                            }
                        }
                    }
                }
                &.child {
                    margin-top: 15px;
                    padding-top: 15px;
                    margin-left: 58px;
                    border-top: 1px solid rgba($color: #2a2d44, $alpha: 0.1);
                }
            }
        }
        .load-comment {
            margin-top: 20px;
            a {
                color: $blue;
                &:hover {
                    color: $copper;
                }
            }
        }
    }
}
.news-social-listing {
    margin-bottom: 30px;
    a {
        &:not(:last-of-type) {
            margin-bottom: 3px;
        }
        display: flex;
        align-items: center;
        height: 2.5rem;
        padding: 0 0.9375rem;
        border-radius: 0.125rem;
        .social-icon {
            width: 1.5625rem;
            font-size: 0.9375rem;
            margin-right: 0.625rem;
        }
        .social-text {
            font-size: 0.625rem;
            text-transform: uppercase;
            letter-spacing: 1px;
        }
        .social-label {
            margin-left: auto;
            font-size: 0.625rem;
            text-transform: uppercase;
            letter-spacing: 1px;
        }
        &.facebook {
            color: #3b5998;
            background-color: rgba(59, 89, 152, 0.1);
        }
        &.instagram {
            color: #fd1d1d;
            background-color: rgba(253, 29, 29, 0.1);
        }
        &.twitter {
            color: #1da1f2;
            background-color: rgba(29, 161, 242, 0.1);
        }
        &.pinterest {
            color: #e60023;
            background-color: rgba(230, 0, 35, 0.1);
        }
    }
}

.page-breadcrumb {
    color: $blue;
    a {
        color: $blue;
    }
    .last {
        color: $yellow;
    }
}
