.about {
    width: 100%;
    overflow-x: hidden;
    &-section {
        padding: 60px 0;
        &-1 {
            padding-top: 120px;
            .col-divider {
                padding-top: 45px;
                padding-bottom: 45px;
                position: relative;
                &:before {
                    content: "";
                    position: absolute;
                    background: #dee2e6;
                    height: 1px;
                    width: 100%;
                }
            }
            .col-title {
                font-size: 16px;
                margin-bottom: 20px;
            }
            .col-inner-right {
                max-width: 400px;
                margin-left: auto;
                width: 100%;
                h2 {
                    color: #333;
                    font-family: "EB Garamond", sans-serif;
                    font-size: 30px;
                    font-weight: 400;
                    line-height: 1.2;
                    margin-bottom: 30px;
                }
            }
        }
        &-2 {
            .col-inner-left {
                max-width: 600px;
                width: 100%;
                h2 {
                    color: #333;
                    font-family: "EB Garamond", sans-serif;
                    font-weight: normal;
                    font-size: 60px;
                    line-height: 1.1;
                    margin-bottom: 50px;
                }
            }
        }
        &-3 {
            .col-inner-right {
                max-width: 570px;
                margin-left: auto;
                width: 100%;
                h2 {
                    color: #333;
                    font-size: 60px;
                    font-family: "EB Garamond", sans-serif;
                    font-weight: normal;
                    line-height: 1.1;
                    margin-bottom: 50px;
                }
            }
        }
        &-4 {
            h2 {
                color: #333;
                font-family: "EB Garamond", sans-serif;
                font-size: 30px;
                font-weight: normal;
                line-height: 1.2;
                text-align: center;
                max-width: 760px;
                margin: 0 auto 30px;
            }
            p {
                font-family: -apple-system, -apple-system, BlinkMacSystemFont, Roboto, Oxygen, Ubuntu, Cantarell,
                    "Open Sans", "Helvetica Neue", sans-serif;
                font-weight: 500;
                font-size: 14px;
                text-align: center;
                text-transform: uppercase;
            }
        }
        &-5 {
            .box-images {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                margin-bottom: 100px;
                img {
                    max-width: 470px;
                    width: 100%;
                }
            }
            .box-order {
                .order-item {
                    display: flex;
                    flex-wrap: wrap;
                    align-items: flex-start;
                    margin-bottom: 80px;
                    .number {
                        font-family: -apple-system, -apple-system, BlinkMacSystemFont, Roboto, Oxygen, Ubuntu, Cantarell,
                            "Open Sans", "Helvetica Neue", sans-serif;
                        color: #e6e6e6;
                        font-size: 48px;
                        font-weight: 600;
                        width: 100px;
                        margin-right: 100px;
                    }
                    .content {
                        flex: 1;
                    }
                }
            }
        }
    }
    &-heading {
        height: 90vh;
        position: relative;
        overflow: hidden;
        .container {
            height: 100%;
        }
        .module-block {
            &.text {
                position: relative;
                height: 100%;
                z-index: 1;
                .sub-title {
                    color: #fff;
                    font-family: -apple-system, -apple-system, BlinkMacSystemFont, Roboto, Oxygen, Ubuntu, Cantarell,
                        "Open Sans", "Helvetica Neue", sans-serif;
                    font-weight: 500;
                    font-size: 14px;
                    text-transform: uppercase;
                    margin-bottom: 30px;
                }
                .entry-title {
                    color: #fff;
                    font-family: "EB Garamond", sans-serif;
                    font-weight: 500;
                    font-size: 80px;
                    line-height: 1.2;
                }
            }
            &.image {
                position: absolute;
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
                img {
                    height: 100%;
                    width: 100%;
                    object-fit: cover;
                }
            }
        }
    }
    &-feedback {
        padding: 60px 0;
        &-desc {
            max-width: 540px;
            margin: 0 auto 40px;
            text-align: center;
        }
        .section-title {
            color: $copper;
            text-align: center;
            margin-bottom: 16px;
        }
        .feedback-item {
            background: #f8f8f8;
            padding: 30px;
            &-top {
                display: flex;
                align-items: center;
                margin-bottom: 20px;
                .avatar {
                    margin-right: 30px;
                    img {
                        border-radius: 50%;
                        height: 88px;
                        width: 88px;
                    }
                }
                .name {
                    font-size: 24px;
                    font-weight: 600;
                    margin-bottom: 4px;
                }
                .stars {
                    color: $copper;
                }
            }
        }
        .owl-nav {
            button {
                background: #fff !important;
                box-shadow: 0px 4px 14px 1px rgba(0, 0, 0, 0.06);
                border-radius: 50%;
                height: 48px;
                width: 48px;
                span {
                    display: none;
                }
                &:hover {
                    color: $copper;
                }
            }
            .owl-prev {
                position: absolute;
                top: 50%;
                left: -24px;
                transform: translateY(-50%);
                &:before {
                    content: "\f053";
                    font-family: "Font Awesome 5 Pro";
                }
            }
            .owl-next {
                position: absolute;
                top: 50%;
                right: -24px;
                transform: translateY(-50%);
                &:before {
                    content: "\f054";
                    font-family: "Font Awesome 5 Pro";
                }
            }
        }
    }
}

@media (max-width: 991px) {
    .about {
        &-heading {
            height: 350px;
            .module-block.text {
                .sub-title {
                    text-align: center;
                    margin-bottom: 20px;
                }
                .entry-title {
                    font-size: 36px;
                    text-align: center;
                }
            }
        }
        &-section {
            padding: 20px 0;
            &-1 {
                padding-top: 30px;
                .col-divider {
                    padding-top: 20px;
                    padding-bottom: 20px;
                }
                .col-inner-right {
                    max-width: 100%;
                }
            }
            &-2 {
                .col-inner-left {
                    max-width: 100%;
                    h2 {
                        font-size: 30px;
                    }
                }
            }
            &-3 {
                .col-inner-right {
                    max-width: 100%;
                    h2 {
                        font-size: 30px;
                        margin-bottom: 30px;
                    }
                }
            }
            &-5 {
                .box-images {
                    justify-content: center;
                    gap: 30px;
                    margin-bottom: 40px;
                }
                .box-order {
                    .order-item {
                        .number {
                            width: 60px;
                            margin-right: 60px;
                        }
                    }
                }
            }
        }
        &-feedback {
            .owl-nav {
                .owl-prev {
                    left: 0;
                }
                .owl-next {
                    right: 0;
                }
            }
        }
    }
}
@media (max-width: 767px) {
    .about {
        &-section {
            &-5 {
                .box-order {
                    .order-item {
                        margin-bottom: 40px;
                        .number {
                            width: 100%;
                            margin-right: 0;
                            margin-bottom: 20px;
                        }
                    }
                }
            }
        }
    }
}
