.lgi-page {
    &-box {
        max-width: 520px;
        margin: 50px auto;
        padding: 32px;
    }
    &-form {
        label {
            display: block;
            margin-bottom: 4px;
            font-weight: 600;
            span {
                color: red;
            }
        }
        .form-group {
            position: relative;
            .show-password {
                cursor: pointer;
                opacity: 0.5;
                position: absolute;
                top: 40px;
                right: 16px;
                .ic-eye-slashed {
                    background: transparent url(../images/ic-eye-slashed.svg) center no-repeat;
                    background-size: contain;
                    display: inline-block;
                    height: 24px;
                    width: 24px;
                }
                .ic-eye-opened {
                    background: transparent url(../images/ic-eye-opened.svg) center no-repeat;
                    background-size: contain;
                    display: inline-block;
                    height: 24px;
                    width: 24px;
                }
            }
            .form-control {
                height: 50px;
                margin-bottom: 24px;
            }
        }
        .password-field {
            .form-control {
                padding-right: 48px;
            }
        }

        .form-action {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 32px;
            .form-check {
                margin: 0;
                &-label {
                    font-weight: normal;
                    margin: 0;
                }
            }
            .form-link {
                border-bottom: 1px dashed $blue;
                > a {
                    color: $blue;
                }
            }
        }
        .form-submit {
            .btn {
                background: $blue;
                border-color: $blue;
                height: 50px;
                width: 100%;
                &:hover {
                    background: $yellow;
                    border-color: $yellow;
                    color: #2d2d2d;
                }
            }
        }
        .form-text {
            margin-top: 16px;
            margin-bottom: 24px;
            line-height: 24px;
            text-align: center;
            > a {
                display: inline-block;
                color: $blue;
                position: relative;
                &:after {
                    content: "";
                    width: 100%;
                    height: 1px;
                    position: absolute;
                    bottom: 1px;
                    left: 0;
                    border-bottom: 1px dashed $blue;
                }
            }
        }
        .divide-text {
            margin: 1.5rem 0;
            position: relative;
            text-align: center;
            span {
                display: inline-block;
                color: #666;
                padding: 0 20px;
                background: #ffffff;
                position: relative;
            }
            &:before {
                content: "";
                background: $yellow;
                position: absolute;
                top: 50%;
                left: 50%;
                pointer-events: none;
                transform: translateX(-50%);
                height: 1px;
                width: 100%;
            }
        }
        .list-social {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            > li {
                width: calc(100% / 2 - 8px);
                img {
                    margin-right: 10px;
                    height: 20px;
                    width: auto;
                }
                > a {
                    border-radius: 4px;
                    color: #fff;
                    display: inline-flex;
                    align-items: center;
                    justify-content: center;
                    height: 50px;
                    width: 100%;
                    &:hover {
                        filter: brightness(110%);
                    }
                }
                &:nth-of-type(1) {
                    > a {
                        background: #1877f2;
                    }
                }
                &:nth-of-type(2) {
                    > a {
                        background: #d0404f;
                    }
                }
            }
        }
    }
}

@media (max-width: 767px) {
    .lgi-page {
        &-box {
            padding: 24px;
            h1 {
                font-size: 28px;
                margin-bottom: 20px;
            }
        }
        &-form {
            .list-social {
                > li {
                    width: 100%;
                    &:nth-child(1) {
                        margin-bottom: 8px;
                    }
                }
            }
        }
    }
}
