.account {
    &-section {
        background: rgba(46, 54, 143, 0.1);
        border-radius: 8px;
        padding: 25px;
        margin-bottom: 24px;
        position: relative;
        h3 {
            font-size: 20px;
            margin-bottom: 16px;
        }
        .form-group {
            label {
                font-weight: 600;
                font-size: 14px;
            }
            .option-wrap {
                display: flex;
                align-items: center;
                label {
                    font-size: 16px;
                    font-weight: normal;
                }
                .form-check {
                    &:first-child {
                        margin-right: 16px;
                    }
                }
            }
        }
        .btn-primary {
            background: $blue;
            border-color: $blue;
            height: 42px;
            padding: 6px 24px;
            &:hover {
                background: #e8bb65;
                border-color: #e8bb65;
                color: #2d2d2d;
            }
        }
        .btn-detail {
            width: 80px;
            color: #fff;
            background: $blue;
            border-color: $blue;
            &:hover {
                background: #e8bb65;
                border-color: #e8bb65;
                color: #2d2d2d;
            }
        }
    }
    &-overview {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        .avatar {
            border-radius: 50%;
            overflow: hidden;
            height: 64px;
            width: 64px;
            margin-right: 20px;
            img {
                height: 100%;
                width: 100%;
            }
        }
        .info {
            width: calc(100% - 84px);
            span,
            strong {
                display: block;
            }
            span {
                color: #666;
                font-style: 14px;
                line-height: 18px;
                margin-bottom: 4px;
            }
            strong {
                font-size: 18px;
                line-height: 24px;
            }
        }
        .text {
            margin-top: 24px;
            width: 100%;
            strong {
                color: #2e368f;
            }
        }
        .register-aff {
            color: white;
            background-color: $blue;
            border-color: $blue;
        }
    }
    &-menu {
        .list {
            > li {
                border-bottom: 1px solid $yellow;
                margin-bottom: 16px;
                padding-bottom: 16px;
                &:last-of-type {
                    border-bottom: 0;
                    margin-bottom: 0;
                    padding-bottom: 0;
                }
                > a {
                    color: #666;
                    line-height: 20px;
                    &:hover,
                    &.active {
                        color: $blue;
                    }
                }
            }
        }
    }
    &-info {
        height: 216px;
        .info-box {
            > li {
                display: flex;
                flex-wrap: wrap;
                &:not(:last-of-type) {
                    margin-bottom: 10px;
                }
            }
            span,
            strong {
                display: inline-block;
            }
            span {
                color: #999;
                margin-right: 12px;
            }
        }
        .action {
            position: absolute;
            top: 12px;
            right: 12px;
            > a {
                border: 1px solid $blue;
                border-radius: 18px;
                color: $blue;
                font-size: 14px;
                display: inline-block;
                padding: 4px 12px;
                &:hover {
                    background: $blue;
                    color: #fff;
                }
            }
        }
    }
    &-order {
        .table {
            font-size: 14px;
            margin-bottom: 0;
            & > :not(:last-child) > :last-child > * {
                border-bottom-color: $yellow;
            }
            &:not(caption) > * > * {
                border-bottom-color: $yellow;
            }
            th {
                color: #666;
                font-weight: normal;
                &:first-of-type {
                    padding-left: 0;
                }
                &:last-of-type {
                    padding-right: 0;
                    text-align: right;
                }
            }
            td {
                &:first-of-type {
                    padding-left: 0;
                }
                &:last-of-type {
                    padding-right: 0;
                    text-align: right;
                }
            }
            tr:last-of-type {
                td {
                    border-bottom: 0;
                }
            }
            .order-item {
                margin-bottom: 0.75rem;
                .title{
                    font-weight: bold;
                }
                .gift{
                    color:#2e368f;
                }
                span {
                    color: #666;
                    display: inline-block;
                    margin-top: 4px;
                }
                &:last-of-type {
                    margin-bottom: 0;
                }
            }
        }
    }
    &-address {
        > .action {
            position: absolute;
            top: 12px;
            right: 12px;
            > a {
                border: 1px solid $blue;
                border-radius: 18px;
                color: $blue;
                font-size: 14px;
                display: inline-block;
                padding: 4px 12px;
                &:hover {
                    background: $blue;
                    color: #fff;
                }
            }
        }
        .address-item {
            border-bottom: 1px solid $yellow;
            margin-bottom: 16px;
            padding-bottom: 16px;
            position: relative;
            &:last-child {
                border-bottom: 0;
                margin-bottom: 0;
            }
            .title {
                font-weight: 600;
                line-height: 22px;
                margin-bottom: 10px;
            }
            .detail {
                font-size: 14px;
                line-height: 18px;
                text-transform: capitalize;
                width: calc(100% - 100px);
            }
            .default {
                color: $blue;
                font-size: 12px;
                font-weight: 600;
                position: absolute;
                bottom: 16px;
                right: 0;
            }
            .action {
                position: absolute;
                right: 6px;
                bottom: 12px;
                > a {
                    display: inline-block;
                    height: 8px;
                    width: 16px;
                    background: transparent
                        url('data:image/svg+xml,<svg width="12" height="7" viewBox="0 0 12 7" fill="none" xmlns="http://www.w3.org/2000/svg"> <path d="M1 1L6 6L11 1" stroke="%23666" stroke-width="1.2" stroke-linecap="round" stroke-linejoin="round"/> </svg>')
                        center no-repeat;
                    background-size: contain;
                    &.rotate {
                        transform: rotate(180deg);
                    }
                }
                &-box {
                    background: #fff;
                    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.12);
                    border-radius: 4px;
                    position: absolute;
                    padding: 16px;
                    right: -24px;
                    top: calc(100% + 4px);
                    width: 170px;
                    z-index: 1;

                    display: none;
                    > a {
                        color: #2d2d2d;
                        display: block;
                        font-size: 14px;
                        line-height: 18px;
                        &:not(:last-of-type) {
                            margin-bottom: 8px;
                        }
                        &:hover {
                            color: $blue;
                        }
                    }
                }
            }
        }
    }
    &-gift {
        .table {
            font-size: 14px;
            margin-bottom: 0;
            & > :not(:last-child) > :last-child > * {
                border-bottom-color: $yellow;
            }
            &:not(caption) > * > * {
                border-bottom-color: $yellow;
            }
            th {
                color: #666;
                font-weight: normal;
                &:first-of-type {
                    padding-left: 0;
                }
                &:last-of-type {
                    padding-right: 0;
                    text-align: right;
                }
            }
            td {
                &:first-of-type {
                    padding-left: 0;
                }
                &:last-of-type {
                    padding-right: 0;
                    text-align: right;
                }
            }
            tr:last-of-type {
                td {
                    border-bottom: 0;
                }
            }
        }
    }
    &-update-info {
        h3 {
            margin-bottom: 24px;
        }
        .avatar-preview {
            img {
                border-radius: 50%;
                margin-top: 1rem;
                height: 100px;
                width: 100px;
            }
        }
    }
    &-change-pass {
        h3 {
            margin-bottom: 24px;
        }
        .form-group {
            position: relative;
            .show-password {
                cursor: pointer;
                display: flex;
                opacity: 0.5;
                position: absolute;
                top: 34px;
                right: 16px;
                .ic-eye-slashed {
                    background: transparent url(../images/ic-eye-slashed.svg) center no-repeat;
                    background-size: contain;
                    display: inline-block;
                    height: 24px;
                    width: 24px;
                }
                .ic-eye-opened {
                    background: transparent url(../images/ic-eye-opened.svg) center no-repeat;
                    background-size: contain;
                    display: inline-block;
                    height: 24px;
                    width: 24px;
                }
            }
        }
    }
}
@media (max-width: 991px) {
    .account {
        &-section {
            padding: 24px;
        }
        &-info {
            height: auto;
        }
        &-order {
            .table {
                width: 650px;
            }
        }
        &-address {
            .address-item {
                &:last-child {
                    padding-bottom: 0;
                }
                .detail {
                    margin-bottom: 10px;
                    width: 100%;
                }
                .default {
                    position: unset;
                }
                .action {
                    line-height: 16px;
                    bottom: auto;
                    right: 6px;
                    top: 0;
                }
            }
        }
        &-gift {
            .table {
                width: 650px;
            }
        }
    }
}
