.limit-text-2 {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
}
.limit-text-3 {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    line-clamp: 3;
    -webkit-box-orient: vertical;
}

.product-item {
    .thumb-box {
        position: relative;
        margin-bottom: 10px;
        .new {
            font-family: -apple-system, -apple-system, BlinkMacSystemFont, Roboto, Oxygen, Ubuntu, Cantarell,
                "Open Sans", "Helvetica Neue", sans-serif;
            background: $blue;
            border-radius: 50%;
            color: #fff;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            font-size: 12px;
            height: 44px;
            width: 44px;
            position: absolute;
            top: 15px;
            right: 15px;
            z-index: 1;
        }
        .sale {
            font-family: -apple-system, -apple-system, BlinkMacSystemFont, Roboto, Oxygen, Ubuntu, Cantarell,
                "Open Sans", "Helvetica Neue", sans-serif;
            background: #b8823b;
            border-radius: 50%;
            color: #fff;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            font-size: 12px;
            height: 44px;
            width: 44px;
            position: absolute;
            top: 15px;
            left: 15px;
            z-index: 1;
        }
        .image {
            img {
                aspect-ratio: 1 / 1;
            }
            .second-thumbnail{
                position: absolute;
                width: 100%;
                height: 100%;
                background-size: cover;
                background-repeat: no-repeat;
                display: block;
                opacity: 0;
                -webkit-transition: opacity 0.25s cubic-bezier(.455,.03,.515,.955);
                transition: opacity 0.25s cubic-bezier(.455,.03,.515,.955);
            }
        }
        .action {
            // background: rgba($color: $copper, $alpha: 0.85);
            display: flex;
            align-items: center;
            justify-content: center;
            font-family: -apple-system, -apple-system, BlinkMacSystemFont, Roboto, Oxygen, Ubuntu, Cantarell,
                "Open Sans", "Helvetica Neue", sans-serif;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            transition: 0.5s;
            height: 100%;
            width: 100%;
            pointer-events: none;
            opacity: 0;
            visibility: hidden;
            .add-to-cart {
                pointer-events: all;
                background: #fff;
                color: $blue;
                font-size: 13px;
                display: flex;
                position: absolute;
                align-items: center;
                justify-content: center;
                width: 36px;
                height: 36px;
                top: calc(72px + 1.4rem);
                right: 10px;
                opacity: 0;
                will-change: transform;
                -webkit-transform: translateX(0.625rem);
                transform: translateX(0.625rem);
                -webkit-transition: all 0.35s cubic-bezier(0.645, 0.045, 0.355, 1);
                transition: all 0.35s cubic-bezier(0.645, 0.045, 0.355, 1);
                &:before {
                    content: "";
                    display: inline-block;
                    background-image: url(../images/icon/ic-cart-blue.svg);
                    height: 18px;
                    width: 18px;
                }
                &:hover {
                    background: $blue;
                    color: #fff;
                    &:before {
                        background-image: url(../images/icon/ic-cart-white.svg);
                    }
                }
            }
            .quick-view {
                pointer-events: all;
                background: #fff;
                color: $blue;
                position: absolute;
                font-size: 14px;
                top: 10px;
                right: 10px;
                display: inline-flex;
                align-items: center;
                justify-content: center;
                height: 36px;
                width: 36px;
                opacity: 0;
                will-change: transform;
                -webkit-transform: translateX(0.625rem);
                transform: translateX(0.625rem);
                -webkit-transition: all 0.35s cubic-bezier(0.645, 0.045, 0.355, 1);
                transition: all 0.35s cubic-bezier(0.645, 0.045, 0.355, 1);
                &:hover {
                    background: $blue;
                    color: #fff;
                }
            }
            .add-to-wishlist {
                pointer-events: all;
                background: #fff;
                color: $blue;
                position: absolute;
                top: calc(36px + 1rem);
                right: 10px;
                display: inline-flex;
                align-items: center;
                justify-content: center;
                height: 36px;
                width: 36px;
                opacity: 0;
                will-change: transform;
                -webkit-transform: translateX(0.625rem);
                transform: translateX(0.625rem);
                -webkit-transition: all 0.35s cubic-bezier(0.645, 0.045, 0.355, 1);
                transition: all 0.35s cubic-bezier(0.645, 0.045, 0.355, 1);
                &:hover {
                    background: $blue;
                    color: #fff;
                }
                &.active {
                    background: #fff;
                    i {
                        color: red;
                        font-weight: 900;
                    }
                    &:hover {
                        background: $blue;
                        i {
                            color: #fff;
                        }
                    }
                }
            }
        }
        &:hover {
            .action {
                opacity: 1;
                visibility: visible;
                .add-to-cart{
                    opacity: 1;
                    will-change: transform;
                    -webkit-transform: translateX(0);
                    transform: translateX(0);
                    -webkit-transition-delay: 0.12s;
                    transition-delay: 0.12s;
                }
                .quick-view{
                    opacity: 1;
                    visibility: visible;
                    will-change: transform;
                    -webkit-transform: translateX(0);
                    transform: translateX(0);
                }
                .add-to-wishlist{
                    opacity: 1;
                    visibility: visible;
                    will-change: transform;
                    -webkit-transform: translateX(0);
                    transform: translateX(0);
                    -webkit-transition-delay: 0.06s;
                    transition-delay: 0.06s;
                }
            }
            .image {
                .second-thumbnail{
                    opacity: 1;
                }
            }
        }
    }
    .info-box {
        height: 77px;
        margin-bottom: 10px;
        font-size: 15px;
        .category {
            margin-bottom: 4px;
            a {
                display: inline-flex;
                align-items: center;
                position: relative;
                color: $blue;
                font-family: "EB Garamond", sans-serif;
                text-transform: uppercase;
                font-size: 14px;
                &:before {
                    content: "";
                    display: inline-block;
                    background: $blue;
                    margin-right: 0;
                    transition: 0.3s;
                    height: 2px;
                    width: 0;
                    left: 0;
                }
                &:hover {
                    &:before {
                        margin-right: 4px;
                        width: 8px;
                    }
                }
            }
        }
        .title {
            a {
                color: #2a2d44;
                font-weight: 700;
                &:hover {
                    color: $blue;
                }
            }
        }
    }
    .price-box {
        display: flex;
        align-items: baseline;
        .main-price {
            color: $copper;
            font-size: 16px;
            font-weight: 600;
            margin-right: 1rem;
        }
        .old-price {
            color: #b0b0b0;
            font-size: 14px;
        }
    }

    @media (max-width: 767px) {
        .thumb-box {
            .image img {
                aspect-ratio: 1;
            }
            .action {
                opacity: 1;
                visibility: visible;
                .add-to-cart{
                    opacity: 1;
                    transform: translateX(0);
                }
                .add-to-wishlist{
                    opacity: 1;
                    transform: translateX(0);
                }
                .quick-view{
                    opacity: 1;
                    transform: translateX(0);
                }

            }
        }
        .price-box {
            flex-wrap: wrap;
            .main-price {
                font-size: 16px;
                margin-right: 10px;
            }
            .old-price {
                font-size: 12px;
            }
        }
    }
}

.page-heading {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 250px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    .page-breadcrumb {
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 20px;
        a {
            color: #fff;
            &:hover {
                color: $copper;
            }
        }
        .divide {
            margin: 0 4px;
        }
    }
    .page-title {
        color: #fff;
        font-family: "EB Garamond", sans-serif;
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 0px;
        &.title-cate{
            color: $yellow;
        }
    }
}

.page-pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    &.customer-list {
        .page-numbers {
            height: 35px;
            width: 35px;
        }
    }
    .page-numbers {
        color: #525150;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #dcdcdc;
        height: 42px;
        width: 42px;
        &.current {
            background: $blue;
            border-color: $blue;
            color: #fff;
        }
    }
    a.page-numbers:hover {
        border-color: $blue;
        color: $blue;
    }
}

.quickview-backdrop {
    background: rgba($color: #000, $alpha: 0.5);
    display: none;
    align-items: center;
    justify-content: center;
    padding: 0 10px;
    z-index: 999;
    &.show {
        display: flex;
    }
    .close-quickview {
        position: absolute;
        top: 4px;
        right: 4px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        background: #000;
        height: 40px;
        width: 40px;
        font-size: 18px;
        z-index: 1;
    }
    .quickview-box {
        background: #fff;
        padding: 2.5rem;
        max-width: 1170px;
        width: 100%;
        &-inner {
            gap: 30px;
        }
        .col-left {
            width: calc(50% - 15px);
        }
        .col-right {
            width: calc(50% - 15px);
            .product-title {
                font-size: 30px;
                font-weight: bold;
            }
            .product-info {
                .main-price {
                    font-size: 26px;
                }
                .old-price {
                    font-size: 18px;
                }
            }
            .product-introduce {
                .box-info {
                    font-size: 14px;
                }
            }
        }
    }

    .carousel {
        color: #170724;
        margin-bottom: 8px !important;

        --carousel-button-bg: #fff;
        --carousel-button-shadow: 0 2px 1px -1px rgb(0 0 0 / 20%), 0 1px 1px 0 rgb(0 0 0 / 14%),
            0 1px 3px 0 rgb(0 0 0 / 12%);

        --carousel-button-svg-width: 20px;
        --carousel-button-svg-height: 20px;

        --carousel-button-svg-stroke-width: 2.5;
    }
    .carousel .carousel__button:focus {
        outline: none;
    }
    .carousel .carousel__button.is-prev {
        left: 30px;
    }
    .carousel .carousel__button.is-next {
        right: 30px;
    }
    .carousel .carousel__dots {
        color: $copper;
    }
    #mainCarousel1 {
        .carousel__slide {
            width: 100%;
            padding: 0;
            .panzoom__content {
                aspect-ratio: 1 / 0.8;
                object-fit: cover;
            }
        }
    }
    #thumbCarousel1 {
        .carousel__slide {
            width: 90px;
            padding: 8px;
            box-sizing: content-box;
            .panzoom__content {
                aspect-ratio: 1;
                object-fit: cover;
            }
            img {
                box-shadow: 0 0 0 1px #e8e8e8;
            }
        }
        .carousel__track {
            margin: 0 -7px;
        }
    }
    #thumbCarousel1 .carousel__slide img {
        border-radius: 0;
    }
    #thumbCarousel1 .carousel__slide.is-nav-selected img {
        box-shadow: 0 0 0 1px $blue;
    }

    @media (max-width: 991px) {
        .quickview-box {
            padding: 1rem;
            &-inner {
                height: 90vh;
                gap: 0;
                overflow-y: auto;
            }
            .col-left {
                width: 100%;
            }
            .col-right {
                width: 100%;
                .product-title {
                    font-size: 24px;
                }
            }
        }
    }
}

.fixed-actions {
    right: 20px;
    bottom: 60px;
    z-index: 999;
    display: flex;
    flex-direction: column;
    gap: 10px;
    a {
        border-radius: 50%;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        height: 52px;
        width: 52px;
        font-size: 24px;
        &.phone,
        &.mess {
            background: $blue;
            border: 1px solid $blue;
            color: #fff;
        }
        &.go-top {
            background: #fff;
            border: 1px solid $copper;
            color: $copper;
        }
        &:hover {
            background: $copper;
            border-color: $copper;
            color: #fff;
        }
    }
}
