.cart-table {
    &-row {
        display: flex;
        align-items: center;
        background: rgba(46, 54, 143, 0.1);
        border-radius: 4px;
        margin-bottom: 8px;
        &.row-total {
            justify-content: space-between;
            padding: 22px;
            margin-top: 32px;
            margin-bottom: 60px;
            flex-wrap: wrap;
            .total-price {
                display: flex;
                align-items: center;
                .text {
                    font-size: 18px;
                    font-weight: 600;
                    margin-right: 12px;
                }
                .price {
                    color: $blue;
                    font-size: 22px;
                    font-weight: 700;
                }
            }
            .total-gift {
                display: flex;
                align-items: center;
                .text {
                    color: #666;
                    font-size: 16px;
                    font-weight: 600;
                    margin-right: 12px;
                }
                .gift {
                    color: #ffa800;
                    font-weight: 700;
                }
            }
            .make-order {
                display: inline-flex;
                align-items: center;
                justify-content: center;
                background: $blue;
                border-radius: 4px;
                color: #fff;
                font-weight: 600;
                height: 50px;
                width: 240px;
                &:hover {
                    background: #e8bb65;
                    color: #2d2d2d;
                }
            }
        }
    }
    .table-col {
        padding: 16px;
        color: #666;
        .product {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            &-thumb {
                border-radius: 4px;
                margin-right: 10px;
                object-fit: contain;
                height: 100px;
                width: 100px;
            }
            &-detail {
                color: #2d2d2d;
                font-weight: 600;
                width: calc(100% - 110px);
                padding: 10px 0;
                .title {
                    margin-bottom: 10px;
                    &:hover {
                        color: $blue;
                    }
                }
                .price {
                    color: #2d2d2d;
                    font-size: 14px;
                    strong {
                        display: inline-block;
                        font-weight: 600;
                        margin-right: 16px;
                    }
                    del {
                        color: #999;
                        display: inline-block;
                        font-weight: normal;
                    }
                }
            }
        }
        .quantity {
            display: flex;
            border: 1px solid #e5e5e5;
            border-radius: 4px;
            float: left;
            overflow: hidden;
            margin-right: 12px;
            .btn {
                background: #fff;
                border: 0;
                border-radius: 0;
                padding: 0;
                height: 36px;
                width: 36px;
                .minus,
                .plus {
                    display: block;
                    height: 36px;
                    width: 36px;
                    position: relative;
                    margin: 0 auto;
                    &:before {
                        border-radius: 2px;
                        content: "";
                        position: absolute;
                        background: #666;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        height: 1.5px;
                        width: 13px;
                    }
                    &:after {
                        border-radius: 2px;
                        content: "";
                        position: absolute;
                        background: #666;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                    }
                }
                .plus:after {
                    height: 13px;
                    width: 1.5px;
                }
                &:hover {
                    .minus,
                    .plus {
                        &:before,
                        &:after {
                            background: $blue;
                        }
                    }
                }
            }
            .form-control {
                background: #f0f2f9;
                border: 0;
                border-radius: 0;
                height: 36px;
                width: 36px;
                padding: 0;
                text-align: center;
                &::-webkit-outer-spin-button,
                &::-webkit-inner-spin-button {
                    appearance: none;
                }
            }
        }
        .bonus {
            font-size: 14px;
            strong {
                color: #3aad61;
                display: block;
                font-weight: 600;
                text-decoration-line: underline;
            }
        }
        .gift {
            color: #ffa800;
            font-weight: 700;
        }
        .amount {
            color: $blue;
            font-weight: 700;
        }
        .actions {
            > a {
                display: flex;
            }
            display: flex;
            align-items: center;
            justify-content: space-between;
            .ic-heart {
                background: transparent url(../images/ic-heart.svg) center no-repeat;
            }
            .ic-trash {
                background: transparent url(../images/ic-trash.svg) center no-repeat;
            }
            .ic-heart,
            .ic-trash {
                background-size: contain;
                display: inline-block;
                height: 20px;
                width: 20px;
            }
            .ic-heart {
                width: 22px;
            }
        }
        &-1 {
            flex: 1;
        }
        &-2 {
            width: 300px;
            .gift {
                display: none;
            }
        }
        &-3 {
            width: 130px;
        }
        &-4 {
            width: 150px;
            .actions {
                display: none;
            }
        }
        &-5 {
            width: 100px;
        }
    }
}
.cart {
    .hp-products {
        margin-bottom: 40px;
    }
}

@media (max-width: 991px) {
    .cart-table {
        .table-col {
            .quantity {
                width: 112px;
                float: unset;
                margin-bottom: 6px;
            }
            .bonus {
                margin-bottom: 6px;
            }
            &-2 {
                width: 200px;
                .gift {
                    display: block;
                    font-size: 14px;
                    margin-bottom: 4px;
                }
                .amount {
                    display: block;
                }
            }
            &-3 {
                display: none;
            }
            &-4 {
                .actions {
                    display: flex;
                    margin-top: 6px;
                    width: 60px;
                    justify-content: space-around;
                }
            }
            &-5 {
                display: none;
            }
        }
        &-row {
            &.row-total {
                padding: 24px;
                .make-order {
                    height: 50px;
                    width: 100%;
                }
                .total-price {
                    .text {
                        font-size: 14px;
                    }
                    .price {
                        font-size: 18px;
                    }
                }
                .total-gift {
                    .text {
                        font-size: 14px;
                    }
                    .gift {
                        font-size: 16px;
                    }
                }
                .col-left,
                .col-right {
                    width: 100%;
                }
                .col-left {
                    margin-bottom: 15px;
                }
            }
        }
    }
}
@media (max-width: 767px) {
    .cart-table {
        .table-col {
            padding: 10px;
            font-size: 12px;
            .product {
                justify-content: center;
                &-thumb {
                    height: 65px;
                    width: 65px;
                    margin: 0;
                }
                &-detail {
                    width: 100%;
                    .title {
                        font-size: 13px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        display: -webkit-box;
                        -webkit-line-clamp: 2;
                        line-clamp: 2;
                        -webkit-box-orient: vertical;
                        margin-bottom: 4px;
                    }
                    .price {
                        font-size: 12px;
                    }
                }
            }
            .bonus {
                font-size: 12px;
            }
            .gift {
                font-size: 12px;
            }
            .quantity {
                width: auto;
                .btn {
                    height: 32px;
                    width: 32px;
                    .minus,
                    .plus {
                        height: 32px;
                        width: 32px;
                        &:before {
                            width: 10px;
                        }
                    }
                    .plus:after {
                        height: 10px;
                    }
                }
                .form-control {
                    height: 32px;
                    width: 32px;
                }
            }
            &-2 {
                width: max-content;
            }
            &-4 {
                width: max-content;
            }
        }
    }
}
