.checkout {
    overflow-x: hidden;
    width: 100%;
    &-section {
        background: #2e368f1a;
        border-radius: 8px;
        margin-bottom: 24px;
        .section-head {
            padding: 22px 32px;
            color: $blue;
            border-bottom: 1px solid #ccced4;
            h2 {
                font-size: 20px;
                margin-bottom: 0;
            }
            .desc {
                color: #666;
                margin-top: 10px;
            }
        }
        .section-content {
            padding: 32px;
            .form-group {
                display: flex;
                flex-wrap: wrap;
                margin-bottom: 24px;
                &:last-child {
                    margin-bottom: 0;
                }
                .label {
                    display: flex;
                    align-items: center;
                    font-size: 16px;
                    line-height: 20px;
                    font-weight: 600;
                    height: 42px;
                    width: 180px;
                }
                .field {
                    flex: 1;
                    .form-control {
                        resize: none;
                    }
                }
            }
            .form-check {
                line-height: 28px;
                margin-bottom: 16px;
                &-input {
                    margin-left: -24px;
                    &:checked[type="radio"] {
                        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'%3e%3cpath fill='none' stroke='%23fff' stroke-linecap='round' stroke-linejoin='round' stroke-width='3' d='M6 10l3 3l6-6'/%3e%3c/svg%3e");
                    }
                }
                &-input[type="radio"] {
                    font-size: 20px;
                    border-radius: 4px;
                }
                &-label {
                    margin-left: 10px;
                }
            }
            .payment-info {
                background: #ffffff;
                border-radius: 4px;
                padding: 24px;
                margin-bottom: 16px;
                .text {
                    color: #666;
                    font-weight: 600;
                    margin-bottom: 10px;
                }
                .payment-detail {
                    > li {
                        display: flex;
                        align-items: center;
                        &:not(:last-of-type) {
                            margin-bottom: 8px;
                        }
                        span {
                            color: #666;
                            display: inline-block;
                            font-size: 14px;
                            width: 115px;
                        }
                        strong {
                            display: inline-block;
                            flex: 1;
                        }
                    }
                }
            }
            &.payment-method {
                padding: 24px 32px 16px;
            }
        }
        &.checkout-success {
            max-width: 500px;
            margin: 3rem auto 3rem;
            h1 {
                color: #36a451;
                font-size: 28px;
                font-weight: 700;
                margin-bottom: 24px;
            }
            .icon {
                margin-bottom: 3rem;
                margin-top: 3rem;
            }
            .action {
                > a {
                    background: $blue;
                    border-radius: 4px;
                    color: #fff;
                    display: flex;
                    font-weight: 600;
                    height: 50px;
                    align-items: center;
                    justify-content: center;
                    &:hover {
                        color: #2d2d2d;
                        background: $yellow;
                    }
                }
            }
            .section-content {
                color: #666;
                text-align: center;
            }
        }
    }
    &-sidebar {
        .sb-box {
            background: #2e368f1a;
            border-radius: 8px;
            margin-bottom: 24px;
            &-header {
                padding: 12px 22px;
                border-bottom: 1px solid #ccced4;
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                justify-content: space-between;
                h3 {
                    margin-bottom: 0;
                    font-size: 20px;
                }
                a {
                    border: 1px solid $blue;
                    border-radius: 18px;
                    padding: 4px 12px;
                    display: inline-block;
                    font-size: 13px;
                    font-weight: 600;
                    color: $blue;
                    &:hover {
                        background: $blue;
                        color: #fff;
                    }
                }
            }
            &-content {
                .sb-item {
                    padding: 16px 32px 16px;
                    border-bottom: 1px solid #ccced4;
                    .product {
                        display: flex;
                        margin-bottom: 16px;
                        img {
                            height: 60px;
                            width: 60px;
                            margin-right: 10px;
                        }
                        .title {
                            color: #2d2d2d;
                            font-size: 14px;
                            font-weight: 600;
                            line-height: 20px;
                            &:hover {
                                color: $blue;
                            }
                        }
                    }
                    .price {
                        font-size: 14px;
                        display: flex;
                        justify-content: space-between;
                        margin-left: auto;
                        width: calc(100% - 70px);
                        .amout {
                            color: #999;
                        }
                        .quantity {
                            color: #666;
                        }
                    }
                }
                .sb-summary {
                    display: flex;
                    align-items: center;
                    padding: 12px 22px;
                    justify-content: space-between;
                    .left {
                        color: #666;
                        font-size: 14px;
                        font-weight: 600;
                    }
                    .right {
                        color: $blue;
                        font-size: 18px;
                        font-weight: 700;
                    }
                }
                .order-summary {
                    border-bottom: 1px solid #ccced4;
                    display: flex;
                    flex-wrap: wrap;
                    align-items: center;
                    justify-content: space-between;
                    font-size: 14px;
                    font-weight: 600;
                    padding: 20px 22px 16px;
                    .left {
                        color: #666;
                        margin-bottom: 16px;
                        width: 50%;
                    }
                    .right {
                        margin-bottom: 16px;
                        text-align: right;
                        width: 50%;
                    }
                }
                .checkout {
                    padding: 0 22px 22px;
                    > a {
                        background: $blue;
                        border: 0;
                        color: #fff;
                        display: inline-flex;
                        align-items: center;
                        justify-content: center;
                        height: 50px;
                        font-weight: 600;
                        text-align: center;
                        width: 100%;
                        &:hover {
                            background: $yellow;
                            color: #2d2d2d;
                        }
                    }
                }
                .coupon{
                    > a {
                    border: 0;
                    background: $blue;
                    color: #fff;
                    align-items: center;
                    justify-content: center;
                    &:hover {
                        background: $yellow;
                        color: #2d2d2d;
                    }
                    }
                }
            }
        }
    }
}
@media (max-width: 1199px) {
    .checkout {
        &-sidebar {
            .sb-box {
                &-header {
                    padding: 16px;
                    h3 {
                        width: 100%;
                    }
                    a {
                        margin-top: 6px;
                    }
                }
                &-content {
                    .sb-item {
                        padding: 16px;
                        .product {
                            .title {
                                overflow: hidden;
                                text-overflow: ellipsis;
                                display: -webkit-box;
                                -webkit-line-clamp: 3;
                                line-clamp: 3;
                                -webkit-box-orient: vertical;
                            }
                        }
                    }
                    .sb-summary {
                        padding: 16px;
                    }
                    .order-summary {
                        padding: 16px 16px 4px;
                    }
                    .checkout {
                        padding: 0 16px 16px;
                        > a {
                            height: 52px;
                        }
                    }
                }
            }
        }
    }
}
@media (max-width: 767px) {
    .checkout {
        &-section {
            .section-head {
                padding: 24px;
            }
            .section-content {
                padding: 24px;
                .form-group {
                    .label {
                        margin-bottom: 6px;
                        height: auto;
                        width: 100%;
                    }
                }
                &.payment-method {
                    padding: 24px 24px 16px;
                }
                .payment-info {
                    padding: 16px;
                    .payment-detail {
                        > li {
                            flex-wrap: wrap;
                            span {
                                width: 100%;
                            }
                            strong {
                                flex: unset;
                                width: 100%;
                            }
                        }
                    }
                }
            }
            &.checkout-success {
                margin: 2rem auto 2rem;
                h1 {
                    font-size: 24px;
                }
                .icon {
                    margin-bottom: 64px;
                    margin-top: 32px;
                }
            }
        }
    }
}
