.contact-page {
    padding: 60px 0;
    width: 100%;
    overflow-x: hidden;
    &-title {
        color: $blue;
        font-family: "EB Garamond", sans-serif;
        font-size: 20px;
        margin-bottom: 20px;
        text-transform: uppercase;
    }
    .contact-form {
        &-inner {
            gap: 20px;
            margin-bottom: 20px;
        }
        &-submit {
            .btn-submit {
                border-radius: 0;
                background: $copper;
                color: #fff;
                padding: 10px 20px;
                text-transform: uppercase;
                margin: 10px 0;
                &:hover {
                    background: $blue;
                }
            }
        }
        .form-group {
            width: calc(100% / 2 - 10px);
            @media (max-width: 767px) {
                width: 100%;
            }
            label {
                display: block;
                font-size: 14px;
                font-weight: 600;
                margin-bottom: 6px;
            }
            .form-control {
                padding: 8px 12px;
            }
        }
    }
    .info-box {
        padding: 30px;
        background: $blue;
        color: #fff;
        .info-item {
            display: flex;
            align-items: center;
            &:not(:last-of-type) {
                margin-bottom: 24px;
            }
        }
        .icon {
            margin-right: 1rem;
            img {
                height: 16px;
                width: 16px;
            }
        }
    }
}
.map-location {
    iframe {
        height: 550px;
        width: 100%;
        @media (max-width: 767px) {
            height: 400px;
        }
    }
}
