.product-listing {
    &-container {
        padding: 40px 0 60px;
    }

    .product-filter {
        .filter-mobile{
            display: none;
        }
        .filter-title {
            font-size: 15px;
            font-family: "EB Garamond", sans-serif;
            padding-bottom: 10px;
            border-bottom: 1px solid #e9e9e9;
            margin-bottom: 10px;
        }
        .filter-check{
            ul {
                padding: 0;
                list-style: none;
                li {
                    padding-bottom: 0.5rem;
                    font-size: .875rem;
                }
                &.filter-check-nav-list{
                    li {
                        &:hover{
                            .type-button{
                                span.button-box:after {
                                    opacity: 1;
                                }
                            }
                        }
                        &.filter-check-nav-list-item{
                            &.active{
                                .type-button{
                                    span.button-box:after {
                                        opacity: 1;
                                    }
                                }
                            }
                        }
                    }
                    .type-button{
                        padding-bottom: 5px;
                        padding-top: 7px;
                        // text-transform: capitalize;
                        position: relative;
                        span.button-box{
                            width: 16px;
                            height: 16px;
                            position: relative;
                            display: inline-block;
                            background-repeat: no-repeat;
                            vertical-align: middle;
                            margin-right: 10px;
                            border: 1px solid #cecdcd;
                            position: absolute;
                            left: 0;
                            top: 8px;
                            &::after{
                                align-items: center;
                                justify-content: center;
                                color: #222;
                                font-weight: normal;
                                line-height: 1;
                                opacity: 0;
                                transition: opacity .2s ease;
                                content: '';
                                position: absolute;
                                top: 4px;
                                left: 3px;
                                height: 3px;
                                width: 7px;
                                border: solid #222;
                                border-width: 0 0 2px 2px;
                                -webkit-transform: rotate(-45deg);
                                -ms-transform: rotate(-45deg);
                                transform: rotate(-45deg);
                                -webkit-box-sizing: content-box;
                                box-sizing: content-box;
                            }
                        }
                        a{
                            padding-left: 26px;
                            z-index: 998;
                            position: relative;
                            color: #000;
                        }
                    }
                }
            }
        }
        .form-check {
            margin-bottom: 36px;
            padding-left: 2em;
        }
        .form-check-label {
            font-family: -apple-system, -apple-system, BlinkMacSystemFont, Roboto, Oxygen, Ubuntu, Cantarell,
                "Open Sans", "Helvetica Neue", sans-serif;
            font-size: 14px;
            display: block;
            clear: both;
            margin-bottom: 15px;
            cursor: pointer;
            a {
                color: #525150
            }
        }
        .form-check-input {
            width: 20px;
            height: 20px;
            margin-top: 0;
            margin-left: -2.3em;

            &[type="checkbox"] {
                border-radius: 0;
            }
        }
        .color-selects {
            display: flex;
            gap: 15px;
            margin-bottom: 36px;
            .color-item {
                cursor: pointer;
                display: inline-flex;
                align-items: center;
                justify-content: center;
                height: 25px;
                width: 25px;
                &.active {
                    color: #fff;
                    &:before {
                        content: "\f00c";
                        font-family: "Font Awesome 5 Pro";
                    }
                }
                &.black {
                    background: #000;
                }
                &.blue {
                    background: #3774b9;
                }
                &.brown {
                    background: #754916;
                }
                &.green {
                    background: #377d22;
                }
                &.orange {
                    background: #ec5428;
                }
                &.red {
                    background: #eb3323;
                }
            }
        }
        #slider-range {
            background: #f0f2f9;
            border-width: 0;
            border-radius: 8px;
            .ui-slider-range {
                background: $blue;
            }
            .ui-state-default {
                background: $blue;
                border-color: $blue;
                border-width: 1.5px;
                border-radius: 50%;
                outline: none;
                top: -0.5em;
                font-size: .6rem;
            }
        }
        .ui-slider-horizontal {
            height: 0.1em
        }
        .price_slider_amount{
            .price_label {
                font-size: 12px;
            }
        }
    }
    .product-sorting {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-bottom: 20px;
        .layout-view {
            display: flex;
            gap: 6px;
            img {
                height: 38px;
                width: 38px;
            }
            .grid_view {
                img {
                    &:nth-child(1) {
                        display: inline-block;
                    }
                    &:nth-child(2) {
                        display: none;
                    }
                }

                &.active {
                    pointer-events: none;
                    img {
                        &:nth-child(1) {
                            display: none;
                        }
                        &:nth-child(2) {
                            display: inline-block;
                        }
                    }
                }
            }
            .list_view {
                img {
                    &:nth-child(1) {
                        display: inline-block;
                    }
                    &:nth-child(2) {
                        display: none;
                    }
                }
                &.active {
                    img {
                        &:nth-child(1) {
                            display: none;
                        }
                        &:nth-child(2) {
                            display: inline-block;
                        }
                    }
                }
            }
            .filter-icon-mobile{
                font-family: -apple-system, -apple-system, BlinkMacSystemFont, Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
                width: max-content;
                // margin-left: 10px;
                display: -webkit-box;
                display: -ms-flexbox;
                display: none;
                -webkit-box-align: center;
                -ms-flex-align: center;
                align-items: center;
                -webkit-box-pack: center;
                -ms-flex-pack: center;
                justify-content: center;
                font-size: 14px;
                height: 38px;
                border: 1px solid #ced4da;
                padding: 0 0.75rem;
                cursor: pointer;
                svg{
                    width: 1.125rem;
                    margin-right: 0.625rem;
                }
            }
        }
        .sort {
            // width: 280px;
            .form-select {
                font-family: -apple-system, -apple-system, BlinkMacSystemFont, Roboto, Oxygen, Ubuntu, Cantarell,
                    "Open Sans", "Helvetica Neue", sans-serif;
                font-size: 14px;
                height: 38px;
            }
        }
    }
    .product-wrap {
        display: flex;
        flex-wrap: wrap;
        gap: 40px 30px;
        &.grid {
            .product-item {
                width: calc(100% / 3 - 20px);
                .short-desc {
                    display: none;
                }
                .list-view-action {
                    display: none;
                }
            }
        }
        &.list {
            .product-item {
                width: 100%;
                .thumb-box {
                    margin-bottom: 0;
                    width: 280px;
                    margin-right: 20px;
                    float: left;
                    .image img {
                        aspect-ratio: 3 / 2;
                    }
                    .action {
                        display: none;
                    }
                }
                .info-box {
                    height: auto;
                }
                .price-box {
                    margin-bottom: 4px;
                }
                .short-desc {
                    font-size: 14px;
                    margin-bottom: 10px;
                }
                .list-view-action {
                    font-family: -apple-system, -apple-system, BlinkMacSystemFont, Roboto, Oxygen, Ubuntu, Cantarell,
                        "Open Sans", "Helvetica Neue", sans-serif;
                    a {
                        background: #fff;
                        border: 1px solid #e9e9e9;
                        color: #2e368f;
                        font-size: 14px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        padding: 6px 0;
                        width: 200px;
                        &:hover {
                            background: $blue;
                            border-color: $blue;
                            color: #fff;
                        }
                    }
                }
                .info-box,
                .price-box,
                .short-desc,
                .list-view-action {
                    width: calc(100% - 300px);
                    float: left;
                }
            }
        }
    }

    .page-pagination {
        margin: 40px 0;
    }
}

@media only screen and (max-width: 991.5px) {
    .product-listing {
        .product-filter{
            &.filter-cate-product{
                .filter-mobile{
                    display: flex;
                    .close-sidebar{
                        svg {
                            width: 1.25rem;
                        }
                    }
                }
                position: fixed;
                top: 0;
                left: 0;
                max-width: 18rem;
                width: 100%;
                height: 100%;
                background-color: white;
                padding: 1.25rem;
                will-change: transform;
                z-index: 888;
                overflow: auto;
                transform: translate(-323.2px, 0px);
                -webkit-transition: all 0.35s cubic-bezier(0.645, 0.045, 0.355, 1);
                transition: all 0.35s cubic-bezier(0.645, 0.045, 0.355, 1);
                &.show{
                    transform: translate(0px, 0px);
                }
            } 
        }
        .product-sorting {
            .layout-view {
                .filter-icon-mobile{
                    display: flex;
                }
            }
            
        }
    }
}

@media (max-width: 767px) {
    .product-listing {
        .product-sorting {
            .layout-view {
                width: auto;
                margin-bottom: 10px;
            }
        }
        .product-wrap {
            &.list {
                .product-item {
                    .thumb-box {
                        width: 180px;
                    }
                    .info-box,
                    .price-box {
                        width: calc(100% - 200px);
                    }
                    .short-desc {
                        margin-top: 10px;
                    }
                    .short-desc,
                    .list-view-action {
                        width: 100%;
                    }
                }
            }
            &.grid {
                .product-item {
                    width: calc(100% / 2 - 15px);
                }
            }
        }
    }
}
@media (max-width: 575px) {
    .product-listing {
        .product-wrap {
            gap: 30px 16px;
            &.list {
                .product-item {
                    .thumb-box,
                    .info-box,
                    .price-box {
                        width: 100%;
                    }
                }
            }
            &.grid {
                .product-item {
                    width: calc(100% / 2 - 8px);
                }
            }
        }
    }
}
