.header {
    &-top {
        padding: 10px 0;
        .logo {
            img {
                width: 96px;
                height: auto;
            }
        }
        .middle-col {
            max-width: 600px;
            width: 100%;
        }
        .search-box {
            .form-control {
                height: 42px;
            }
            .btn-submit {
                background: $blue;
                color: #fff;
                font-size: 18px;
                position: absolute;
                top: 0;
                right: 0;
                height: 42px;
                width: 42px;
            }
        }
        .benefit {
            &-item {
                display: flex;
                align-items: center;
                .icon {
                    display: flex;
                    margin-right: 10px;
                    img {
                        width: auto;
                        height: 20px;
                    }
                }
            }
        }
        .my-account {
            color: #939291;
            margin-right: 60px;
            a {
                color: #939291;
                &:hover {
                    color: #1a1d31;
                }
            }
        }
        .shop-cart {
            a {
                color: #1a1d31;
            }
            img {
                height: 20px;
                width: 20px;
            }
            .icon {
                display: inline-block;
            }
            .shop-count {
                background: $copper;
                border-radius: 50%;
                color: #fff;
                display: inline-flex;
                align-items: center;
                justify-content: center;
                position: absolute;
                font-size: 10px;
                height: 16px;
                width: 16px;
                left: 14px;
                top: -2px;
            }
        }
    }
    &-menu {
        background: $blue;
        &-list {
            display: flex;
            > li.menu-item {
                position: relative;
                margin-right: 50px;
                &:last-of-type {
                    margin-right: 0;
                }
                > a {
                    display: inline-block;
                    color: #fff;
                    line-height: 26px;
                    padding: 10px 0;
                }
            }
            > li.menu-item.has-sub {
                > a {
                    &:after {
                        content: "";
                        display: inline-block;
                        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23ffffff' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='M2 5l6 6 6-6'/%3e%3c/svg%3e");
                        background-repeat: no-repeat;
                        background-position: center;
                        margin-left: 2px;
                        height: 10px;
                        width: 16px;
                    }
                }
                > .sub-menu {
                    background: #fff;
                    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
                    padding: 8px 0;
                    position: absolute;
                    left: -1.875rem;
                    min-width: 200px;
                    opacity: 0;
                    visibility: hidden;
                    transition: 0.3s;
                    z-index: 11;
                    > li {
                        > a {
                            color: #1a1d31;
                            display: block;
                            padding: 8px 16px;
                            &:hover {
                                color: $copper;
                            }
                        }
                    }
                }
                &:hover {
                    > .sub-menu {
                        opacity: 1;
                        visibility: visible;
                    }
                }
            }
        }
    }
    &-mobile {
        display: none;
        &-inner {
            .logo {
                img {
                    width: auto;
                    height: 60px;
                }
            }
            .col-left,
            .col-right {
                display: flex;
                font-size: 20px;
                align-items: center;
                width: 62px;
                a {
                    color: #1a1d31;
                }
            }
            .col-right {
                justify-content: flex-end;
            }
            .mobile-nav {
                margin-right: 20px;
            }
            .mobile-cart {
                margin-left: 20px;
                position: relative;
                .shop-count {
                    background: $copper;
                    border-radius: 50%;
                    font-size: 12px;
                    color: #fff;
                    position: absolute;
                    top: 4px;
                    right: -8px;
                    display: inline-flex;
                    align-items: center;
                    justify-content: center;
                    width: 18px;
                    height: 18px;
                }
            }
        }
    }
    &-mobile-content {
        max-width: 320px;
        width: 100%;
        .logo {
            img {
                width: auto;
                height: 60px;
            }
        }
        .search-box {
            .btn-submit {
                background: $blue;
                color: #fff;
                position: absolute;
                right: 0;
                top: 0;
            }
        }
        .mobile-menu-list {
            .menu-item {
                position: relative;
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                justify-content: space-between;
                a {
                    color: #1a1d31;
                    display: flex;
                    font-size: 14px;
                    padding: 10px 0;
                }
                &.has-sub {
                    .sub-menu {
                        position: relative;
                        width: 100%;
                        margin: 0;
                        padding-left: 1.25rem;
                        transition: 0.3s;
                        display: none;
                        a {
                            padding: 6px 0;
                        }
                        &.show {
                            display: block;
                        }
                    }
                    .caret {
                        display: inline-flex;
                        align-items: center;
                        justify-content: center;
                        width: 16px;
                        height: 24px;
                        &:before {
                            content: "";
                            width: 100%;
                            height: 100%;
                            background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-chevron-down"><polyline points="6 9 12 15 18 9"></polyline></svg>');
                            background-size: contain;
                            background-repeat: no-repeat;
                            background-position: center;
                        }
                    }
                }
            }
            > .menu-item + .menu-item {
                border-top: 1px solid #eee;
            }
        }
    }
}
@media (max-width: 1199px) {
    .header {
        &-top {
            .middle-col {
                max-width: 450px;
            }
            .benefit {
                &-item {
                    width: calc(100% / 3);
                }
            }
        }
    }
}
@media (max-width: 991px) {
    .header {
        &-top {
            display: none;
        }
        &-menu {
            display: none;
        }
        &-mobile {
            display: block;
        }
    }
}
