.product-detail {
    padding: 30px 0;
    .page-breadcrumb {
        margin-bottom: 40px;
    }
    &-main {
        margin-top: 60px;
        .nav-pills {
            border-bottom: 1px solid #e5e8ed;
            padding-bottom: 20px;
            margin-bottom: 20px;
            .nav-link {
                border: 1px solid #fff;
                border-radius: 0;
                color: #9a9ca5;
                &.active {
                    background: #fff;
                    border-color: $blue;
                    color: $blue;
                }
            }
        }
    }
    .product-title {
        font-family: "EB Garamond", sans-serif;
        font-size: 2rem;
        margin-bottom: 20px;
    }
    .product-info {
        margin-bottom: 20px;
        .main-price {
            color: $copper;
            font-size: 1.5rem;
            font-weight: bold;
        }
        .old-price {
            color: #787a80;
            font-size: 1rem;
        }
    }
    .product-status {
        color: #4bb633;
        border: 1px solid;
        font-size: .75rem;
        font-weight: 600;
        text-transform: uppercase;
        padding: 0.375rem 0.625rem;
        margin-top: 0.05rem;
        margin-left: 3.125rem
    }

    .product-error {
        color: red;
        border: 1px solid;
        font-size: .75rem;
        font-weight: 600;
        text-transform: uppercase;
        padding: 0.375rem 0.625rem;
        margin-top: 0.05rem;
        margin-left: 3.125rem
    }

    .product-actions {
        .inner-row {
            margin-bottom: 30px;
        }
        label {
            display: block;
            font-weight: bold;
            margin-bottom: 6px;
        }
        .stars {
            .fas {
                color: #f89828;
            }
            .far {
                color: #b3b7bc;
            }
        }
        .selection {
            width: 150px;
        }
        .form-select {
            height: 44px;
        }
        .feedback {
            width: 185px;
            text-align: right;
            .total {
                color: #787a80;
            }
        }
        .quantity {
            width: 15%;
            .form-control {
                height: 44px;
            }
        }
        .add-to-cart {
            width: 46%;
            a {
                display: inline-flex;
                align-items: center;
                justify-content: center;
                background: $blue;
                font-size: 14px;
                font-weight: 700;
                color: #fff;
                height: 44px;
                width: 100%;
                img {
                    margin-right: 8px;
                    height: 16px;
                    width: 16px;
                }
                &:hover {
                    background: $copper;
                }
            }
        }
        .wishlist {
            width: 30%;
            a {
                display: inline-flex;
                align-items: center;
                justify-content: center;
                border: 1px solid $copper;
                background: #fff;
                font-size: 14px;
                font-weight: 700;
                color: $copper;
                height: 44px;
                width: 100%;
                i {
                    margin-right: 8px;
                }
                &:hover {
                    color: #fff;
                    background: $copper;
                }
            }
            &.active {
                a {
                    background: #fff;
                    color: red;
                    i {
                        color: red;
                        font-weight: 900;
                    }
                    &:hover {
                        color: #fff;
                        background: $copper;
                        i {
                            color: #fff;
                        }
                    }
                }
            }
        }
    }
    .product-introduce {
        padding-top: 20px;
        border-top: 1px solid #e5e8ed;
        border-bottom: 1px solid #e5e8ed;
        p {
            font-size: 15px;
        }
        .head {
            font-size: 15px;
        }
        
    }
    .product-meta{
        .box-info {
            padding-bottom: 20px;
            line-height: 2;
            font-size: 14px;
            border-bottom: 1px solid #e5e8ed;
            margin-bottom: 20px;
            a {
                color: #2a2d44;
                font-weight: bold;
                &:hover {
                    color: $copper;
                }
            }
        }
    }
    .product-share {
        display: flex;
        align-items: center;
        gap: 1rem;
        font-size: 14px;
        span {
            display: inline-block;
        }
        a {
            color: $copper;
            &:hover {
                transform: translateY(-4px);
            }
        }
    }
    .tab-content-section {
        max-width: 850px;
        h3 {
            font-size: 20px;
            font-family: "EB Garamond", sans-serif;
            text-transform: uppercase;
        }
    }
    .feedback-form {
        display: flex;
        flex-wrap: wrap;
        gap: 0 16px;
        .form-group {
            width: calc(100% / 2 - 8px);
            strong span {
                color: red;
            }
            input.form-control {
                height: 42px;
            }
        }
        .form-submit {
            .btn {
                border-radius: 0;
                border-color: $blue;
                background: $blue;
                &:hover {
                    background: $copper;
                    border-color: $copper;
                }
            }
        }

        .rate-group {
            display: flex;
            margin-bottom: 16px;
        }
        .select-rate {
            display: flex;
            flex-wrap: wrap;
            margin-left: 10px;
            .ic-star {
                cursor: pointer;
                margin: 0 3px;
                color: #b3b7bc;
                &:before {
                    content: "\f005";
                    font-family: "Font Awesome 5 Pro";
                    font-weight: 500;
                }
                &.active {
                    color: #f89828;
                    &:before {
                        font-weight: 900;
                    }
                }
            }
        }
    }
    .comment-item {
        display: flex;
        margin-bottom: 40px;
        .avatar {
            border-radius: 50%;
            overflow: hidden;
            margin-right: 15px;
            height: 52px;
            width: 52px;
            img {
                border-radius: 50%;
                height: 100%;
                width: 100%;
            }
        }
        .detail {
            flex: 1;
        }
        .rate {
            font-size: 14px;
            margin-bottom: 4px;
            .fas {
                color: #f89828;
            }
            .far {
                color: #b3b7bc;
            }
        }
        .user {
            display: flex;
            margin-bottom: 10px;
            align-items: baseline;
            strong {
                display: inline-block;
                font-size: 16px;
                margin-right: 15px;
                text-transform: capitalize;
            }
            span {
                display: inline-block;
                color: #666;
                font-size: 14px;
            }
        }
        .content {
            font-size: 14px;
        }
        &:last-child {
            margin-bottom: 0;
        }
    }

    .carousel {
        color: #170724;
        margin-bottom: 8px !important;

        --carousel-button-bg: #fff;
        --carousel-button-shadow: 0 2px 1px -1px rgb(0 0 0 / 20%), 0 1px 1px 0 rgb(0 0 0 / 14%),
            0 1px 3px 0 rgb(0 0 0 / 12%);

        --carousel-button-svg-width: 20px;
        --carousel-button-svg-height: 20px;

        --carousel-button-svg-stroke-width: 2.5;
    }
    .carousel .carousel__button:focus {
        outline: none;
    }
    .carousel .carousel__button.is-prev {
        left: 30px;
    }
    .carousel .carousel__button.is-next {
        right: 30px;
    }
    .carousel .carousel__dots {
        color: $copper;
    }
    #mainCarousel {
        .carousel__slide {
            width: 100%;
            padding: 0;
            .panzoom__content {
                aspect-ratio: 1 / 1;
                object-fit: cover;
            }
        }
    }
    #thumbCarousel {
        .carousel__slide {
            width: 90px;
            padding: 8px;
            box-sizing: content-box;
            .panzoom__content {
                aspect-ratio: 1;
                object-fit: cover;
            }
            img {
                box-shadow: 0 0 0 1px #e8e8e8;
            }
        }
        .carousel__track {
            margin: 0 -7px;
        }
    }
    #thumbCarousel .carousel__slide img {
        border-radius: 0;
    }
    #thumbCarousel .carousel__slide.is-nav-selected img {
        box-shadow: 0 0 0 1px $blue;
    }
}
@media (max-width: 767px) {
    .product-detail {
        .product-title {
            font-size: 28px;
        }
        .product-info {
            flex-wrap: wrap;
            .price-box {
                margin-bottom: 4px;
                width: 100%;
            }
        }
        .product-actions {
            .feedback {
                order: -1;
                text-align: left;
                margin-bottom: 20px;
                width: 100%;
            }
        }
        .product-status, .product-error{
            margin-left: 0px;
        }
        &-main {
            margin-top: 40px;
        }
    }
}
