.hp-banner {
    &-item {
        position: relative;
        .banner-image {
            aspect-ratio: 1 / 0.45;
            max-height: 600px;
        }
        .banner-content {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            width: 100%;
            &-head {
                color: $blue;
                font-size: 42px;
                font-family: "EB Garamond", sans-serif;
                font-weight: 600;
                text-transform: uppercase;
                max-width: 512px;
                margin-bottom: 10px;
                width: 100%;
            }
            &-text {
                max-width: 420px;
                margin-bottom: 30px;
                width: 100%;
            }
            &-action {
                a {
                    background: $copper;
                    color: #fff;
                    display: inline-flex;
                    align-items: center;
                    justify-content: center;
                    width: 160px;
                    height: 48px;
                    i {
                        margin-left: 16px;
                    }
                }
            }
        }
    }
    .owl-nav {
        button span {
            display: none;
        }
        .owl-prev {
            position: absolute;
            left: 40px;
            top: 50%;
            transform: translateY(-50%);
            &:before {
                content: "\f053";
                color: #838786;
                font-family: "Font Awesome 5 Pro";
                font-size: 30px;
                font-weight: 300;
            }
        }
        .owl-next {
            position: absolute;
            right: 40px;
            top: 50%;
            transform: translateY(-50%);
            &:before {
                content: "\f054";
                color: #838786;
                font-family: "Font Awesome 5 Pro";
                font-size: 30px;
                font-weight: 300;
            }
        }
    }
    .owl-dots {
        display: flex;
        align-items: center;
        gap: 10px;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 30px;
        .owl-dot {
            border: 1px solid #a2a2a2;
            border-radius: 50%;
            position: relative;
            height: 16px;
            width: 16px;
            &:hover {
                border-color: $copper;
            }
            &.active {
                border-color: $copper;
                &:after {
                    content: "";
                    background: $copper;
                    display: inline-block;
                    border-radius: 50%;
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    height: 8px;
                    width: 8px;
                }
            }
        }
    }
}
.hp-partner {
    padding: 40px 0 60px;
    .container {
        position: relative;
        &:before {
            content: "";
            background: #eaeaea;
            width: 100%;
            height: 1px;
            position: absolute;
            top: -40px;
            left: 0;
            z-index: 99;
        }
    }
    a > img {
        opacity: .3;
    }
    a:hover > img {
        opacity: 1;
    }
}
.hp-about {
    text-align: center;
    margin-bottom: 30px;
    .section-title {
        margin-bottom: 10px;
    }
    .stars {
        color: #f6bb41;
    }
    &-text {
        color: #525150;
        max-width: 540px;
        margin: 0 auto 20px;
    }
}
.hp-category {
    &-title {
        font-family: "EB Garamond", sans-serif;
        font-size: 20px;
        margin-bottom: 20px;
    }
    &-wrap {
        gap: 30px;
        .category-item {
            font-family: "EB Garamond", sans-serif;
            position: relative;
            text-transform: uppercase;
            width: calc(100% / 6 - 25px);
            img {
                aspect-ratio: 1 / 0.8375;
            }
            .text {
                background: rgba($color: $blue, $alpha: 0.8);
                color: #fff;
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 10px;
                position: absolute;
                font-weight: 600;
                width: 100%;
                height: 100%;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                text-align: center;
                transition: 0.3s;
                opacity: 0;
                visibility: hidden;
            }
            &:hover {
                .text {
                    opacity: 1;
                    visibility: visible;
                }
            }
        }
    }
}
.hp-product {
    padding: 80px 0;
    &.has-bg {
        background: rgba($color: $blue, $alpha: 0.05);
    }
    &-head {
        margin-bottom: 20px;
        justify-content: space-between;
        a {
            color: $copper;
            &:hover {
                color: $blue;
            }
        }
    }
    .owl-nav {
        button {
            background: #fff !important;
            box-shadow: 0px 4px 14px 1px rgba(0, 0, 0, 0.06);
            border-radius: 50%;
            height: 48px;
            width: 48px;
            span {
                display: none;
            }
            &:hover {
                color: $copper;
            }
        }
        .owl-prev {
            position: absolute;
            top: calc(50% - 62px);
            left: -24px;
            transform: translateY(-50%);
            &:before {
                content: "\f053";
                font-family: "Font Awesome 5 Pro";
            }
        }
        .owl-next {
            position: absolute;
            top: calc(50% - 62px);
            right: -24px;
            transform: translateY(-50%);
            &:before {
                content: "\f054";
                font-family: "Font Awesome 5 Pro";
            }
        }
    }
}
.hp-gender {
    margin-bottom: 60px;
    &-box {
        align-items: end;
        padding: 10px 20px 0 20px;
        width: calc(100% / 2 - 15px);
        .image {
            margin-right: 30px;
            width: 160px;
        }
        .info {
            flex: 1;
            padding-bottom: 30px;
            &-title {
                color: $copper;
                font-family: "EB Garamond", sans-serif;
                font-size: 24px;
            }
        }
        .desc {
            color: #a2a2a2;
        }
        .action {
            background: $blue;
            color: #fff;
            display: inline-block;
            padding: 8px 16px;
        }
        &.col-left {
            background: #fff6ea;
        }
        &.col-right {
            background: #edefff;
        }
    }
    .sale-countdown {
        font-family: -apple-system, -apple-system, BlinkMacSystemFont, Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
            "Helvetica Neue", sans-serif;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        margin-top: 40px;
        .time-box {
            display: flex;
            align-items: center;
        }
        .separate {
            margin-left: 0.438rem;
            margin-right: 0.625rem;
        }
    }
    .countdown-text {
        p {
            margin: 0;
        }
        margin-right: 1rem;
        a {
            color: #ef4626;
            font-weight: 600;
            text-decoration: underline;
        }
    }
    .countdown-timer {
        display: flex;
        align-items: center;
        .time-box {
            border: 1px solid #333;
            border-radius: 2px;
            padding: 0.313rem 0.625rem;
            width: 180px;
            .text {
                font-size: 1.25rem;
                font-weight: 500;
                min-width: 1.75rem;
            }
        }
    }
    .sec-box {
        border: 1px solid #ef4626;
        border-radius: 2px;
        padding: 0.313rem 0.625rem;
        margin-left: 0.75rem;
        min-width: 60px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #ef4626;
        .text {
            font-size: 1.25rem;
            font-weight: 500;
            min-width: 1.75rem;
        }
    }
}
.hp-news {
    padding: 60px 0 80px;
    height: 640px;
    position: relative;
    display: flex;
    align-items: center;
    .col-left {
        .section-title {
            margin-bottom: 10px;
        }
        p {
            margin-bottom: 20px;
        }
        a {
            display: inline-flex;
            align-items: center;
            justify-content: center;
            background: $copper;
            color: #fff;
            height: 48px;
            width: 160px;
            i {
                margin-left: 16px;
            }
        }
    }
    .col-right {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        padding-right: 0;
    }
    .news-item {
        position: relative;
        margin: 0 10px;
        width: 373px;
        height: 414px;
        transition: 0.3s;
        .thumb {
            height: 100%;
            img {
                aspect-ratio: 1 / 1.1;
                object-fit: cover;
                height: 100%;
            }
        }
        .content-box {
            display: none;
            background: rgba(255, 255, 255, 0.82);
            padding: 20px 24px 24px;
            position: absolute;
            left: 24px;
            bottom: 24px;
            width: 300px;
            .news-cate {
                font-family: "EB Garamond", sans-serif;
                margin-bottom: 8px;
                a {
                    color: $copper;
                }
            }
            .news-title {
                margin-bottom: 10px;
                a {
                    color: #2a2d44;
                    font-size: 15px;
                    font-weight: 700;
                }
            }
            .news-desc {
                font-size: 13px;
            }
            &:after {
                content: "\f061";
                font-weight: 300;
                background: $blue;
                color: #fff;
                font-size: 20px;
                display: inline-flex;
                align-items: center;
                justify-content: center;
                font-family: "Font Awesome 5 Pro";
                height: 48px;
                width: 48px;
                position: absolute;
                right: -56px;
                bottom: 0;
            }
        }
        &.slick-current {
            width: 400px;
            height: 510px;
            .content-box {
                display: block;
            }
        }
    }
    .slick-prev {
        display: none !important;
    }
    .slick-next {
        background: $copper;
        border-radius: 50%;
        right: 100px;
        top: calc(50% - 40px);
        height: 48px;
        width: 48px;
        &:before {
            content: unset;
        }
        &:after {
            content: "\f054";
            display: inline-block;
            width: 10px;
            height: 20px;
            background-image: url('data:image/svg+xml,%3Csvg width="9" height="16" viewBox="0 0 9 16" fill="none" xmlns="http://www.w3.org/2000/svg"%3E%3Cpath d="M1 1L8 8L1 15" stroke="white" stroke-width="1.8" stroke-linecap="round" stroke-linejoin="round"/%3E%3C/svg%3E');
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;
        }
    }
    .slick-dots {
        bottom: 40px;
        padding-left: 400px;
        li {
            border: 1px solid #a2a2a2;
            border-radius: 50%;
            position: relative;
            height: 16px;
            width: 16px;
            &:hover {
                border-color: $copper;
            }
            &.slick-active {
                border-color: $copper;
                &:after {
                    content: "";
                    background: $copper;
                    display: inline-block;
                    border-radius: 50%;
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    height: 8px;
                    width: 8px;
                }
            }
        }
        button {
            display: none;
        }
    }
}
.hp-popular {
    padding: 40px 0 60px;
    .section-title {
        text-align: center;
    }
    .container {
        position: relative;
        &:before {
            content: "";
            background: #eaeaea;
            width: 100%;
            height: 1px;
            position: absolute;
            top: -40px;
            left: 0;
            z-index: 99;
        }
    }
    &-wrap {
        gap: 30px;
        .popular-item {
            position: relative;
            width: calc(100% / 4 - 23px);
            &:hover {
                .popular-image img {
                    transform: scale(1.1);
                }
            }
        }
        .popular-detail {
            position: absolute;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            padding: 0.9375rem;
            z-index: 1;
            .entry-category {
                color: #fff;
                font-family: "EB Garamond", sans-serif;
                font-weight: normal;
            }
            .button-text {
                color: #fff;
                font-family: -apple-system, -apple-system, BlinkMacSystemFont, Roboto, Oxygen, Ubuntu, Cantarell,
                    "Open Sans", "Helvetica Neue", sans-serif;
                font-size: 0.6875rem;
                font-weight: 600;
                text-transform: uppercase;
                padding: 0 0.375rem 0.3125rem;
                border-bottom: 1px solid;
                &:hover {
                    color: #ef4626;
                    border-color: #ef4626;
                }
            }
        }
        .popular-image {
            position: relative;
            overflow: hidden;
            img {
                aspect-ratio: 1;
                transition: 0.3s;
            }
        }
    }
}
@media (max-width: 991px) {
    .hp-category {
        &-wrap {
            .category-item {
                width: calc(100% / 3 - 20px);
            }
        }
    }
    .hp-gender {
        &-box {
            width: 100%;
            &:first-of-type {
                margin-bottom: 30px;
            }
        }
        .countdown-text {
            margin-bottom: 1rem;
            margin-right: 0;
        }
    }
    .hp-news {
        height: auto;
        .col-left {
            margin-bottom: 30px;
        }
        .col-right {
            position: unset;
            transform: unset;
            padding-right: calc(var(--bs-gutter-x) / 2);
        }
    }
    .hp-popular {
        &-wrap {
            gap: 20px;
            .popular-item {
                width: calc(100% / 2 - 10px);
            }
        }
    }
}
@media (max-width: 767px) {
    .hp-banner {
        &-item {
            .banner-content {
                &-head {
                    font-size: 24px;
                }
                &-text {
                    font-size: 14px;
                    margin-bottom: 20px;
                }
                &-action {
                    a {
                        font-size: 14px;
                        width: 120px;
                        height: 36px;
                        i {
                            margin-left: 8px;
                        }
                    }
                }
            }
            .banner-image {
                min-height: 300px;
            }
        }
        .owl-nav {
            display: none;
        }
        .owl-dots {
            bottom: 10px;
        }
    }
    .hp-category {
        &-wrap {
            gap: 20px;
            .category-item {
                width: calc(100% / 2 - 10px);
            }
        }
    }
    .hp-gender {
        &-box {
            .image {
                display: none;
            }
        }
    }
    .hp-product {
        .owl-nav {
            .owl-prev {
                left: 0;
            }
            .owl-next {
                right: 0;
            }
        }
    }
    .hp-news {
        .col-left {
            a {
                font-size: 14px;
                width: 120px;
                height: 36px;
            }
        }
        .news-item {
            width: 320px;
            height: 355px;
            .content-box {
                padding: 16px;
                bottom: 16px;
                left: 16px;
                width: 288px;
                &:after {
                    content: unset;
                }
            }
            &.slick-current {
                width: 320px;
                height: 355px;
            }
        }
        .slick-next {
            display: none !important;
        }
        .slick-dots {
            bottom: -40px;
            padding-left: 0;
        }
    }
    .section-title {
        font-size: 20px;
    }
}

@media (max-width: 480px) {
    .hp-product {
        .owl-nav {
            button {
                height: 38px;
                width: 38px;
            }
        }
    }
}